<template>
  <div class="blog">
    <div class="blog__container container">
      <div class="blog__action">
          <button class="btn blog__load" type="button" @click="loadData()" v-if="!hasPosts">Загрузить посты</button>
          <button class="btn blog__add" type="button" @click="addPost()" v-else>Добавить пост</button>
      </div>
      <hr/>
      <div class="blog__posts" v-if="hasPosts">
        <blog-sort v-model="sortSelect"></blog-sort>
        <div class="blog__post post" v-for="post in paginateData" :key="post.id">
          <div class="post__name">
            <span class="post__subtitle">Название:</span>
            <div class="post__name-post" contenteditable="true" @input="editTitle($event, post.id)">
              {{post.title}}
            </div>
          </div>
          <hr/>
          <div class="post__body" contenteditable="true" :ref="'post'+ post.id" @input="editDescr($event, post.id)">
            {{post.excerpt}}
          </div>
          <button class="post__more btn" type="button" @click.once="getFullText(post.body, post.id)" v-if="post.body!==post.excerpt">
            Полный текст
          </button>
          <div class="post__remove" @click="confirmDelete(post.id)">Удалить</div>
        </div>
        <blog-pagination v-show="hasPagination" :number="pageNumber" :count="pageCount" @toPrev="prevPage" @toNext="nextPage"/>
      </div>
      <div class="blog__info" v-else>
        Пока постов нет
      </div>
    </div>
    <app-modal :title="'Подтвердите удаление'" :class="{'show': modalActive}" @updateParent="updateData">
      <div class="modal__text">Уверены, что хотите удалить данный пост?</div>
      <div class="modal__action">
        <button type="button" class="modal__btn btn" @click="removePost(curPost)">Да</button>
        <button type="button" class="modal__btn btn" @click="modalActive=false">Нет</button>
      </div>
    </app-modal>
  </div>
</template>
<script>
import {mapActions, mapGetters} from 'vuex'
import AppModal from './components/AppModal'
import BlogPagination from './components/BlogPagination.vue'
import BlogSort from './components/BlogSort.vue'
export default {
  name: 'App',
  components: {
    AppModal, BlogPagination, BlogSort
  },
  data(){
    return {
      pageNumber: 0,
      size: 5,
			sortSelect: '',
      modalActive: false,
      curPost: ''
    }
  },
  computed:{
    ...mapGetters(['getPosts']),
    hasPosts(){
      return this.getPosts && this.getPosts.length>0
    },
    hasPagination(){
      return this.getPosts.length>this.size
    },
    pageCount(){
      let lPosts = this.getPosts.length,
          size = this.size;
      return Math.ceil(lPosts/size);
    },
    paginateData(){
      const start = this.pageNumber * this.size,
          end = start + this.size;
      return this.getPosts.slice(start, end);
    }
  },
  methods: {
    ...mapActions(['setPosts','loadData', 'editTitlePost', 'editDescrPost', 'deletePost', 'addPost', 'sortPosts']),
    getFullText(text, id){
      let idPost = 'post'+id;
      let el = this.$refs[idPost];
      el[0].innerText = text
    },
    editTitle(event,post){
      let newTitle = event.target.innerText;
      this.editTitlePost({newTitle, post})
    },
    editDescr(event, post){
      let newDescr = event.target.innerText;
      event.target.onblur = () => {
        this.editDescrPost({newDescr, post});
      }   
    },
    removePost(post){
      this.deletePost(post);
      this.modalActive = false;
    },
    confirmDelete(post){
      this.modalActive = true;
      this.curPost = post;
    },
    nextPage(){
      this.pageNumber++;
    },
    prevPage(){
      this.pageNumber--;
    },
    updateData(data){
      this.modalActive = data.modalActive
    }
  },
  mounted(){
    if(localStorage.getItem('posts')){
      this.setPosts(JSON.parse(localStorage.getItem('posts')))
    }
  },
  watch:{
    sortSelect(newSortSelect){
			this.sortPosts(newSortSelect);
		}
  }
}
</script>

