<template>
    <div class="blog__pagination">
        <button class="btn" :disabled="number==0" @click="toPrev">
        ⇦
        </button>
        <button class="btn" :disabled="number>= count-1" @click="toNext">
        ⇨
        </button>
    </div>
</template>
<script>
export default {
    name: 'BlogPagination',
    props: {
        number: {type: Number},
        count: {type: Number},
    },
    methods: {
        toPrev(){
            this.$emit('toPrev')
        },
        toNext(){
            this.$emit('toNext')
        }
    }
}
</script>