<template>
    <div class="modal">
        <div class="modal__content">
            <div class="modal__header">
                <div class="modal__title">
                    {{title}}
                </div>
                <span class="modal__close" @click="modalClose($event)">&#10006;</span>
            </div>
            <div class="modal__body">
                <slot></slot>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'AppModal',
    props: {
        title: {type: String},
    },
    methods: {
        modalClose(){
            this.$emit('updateParent', {modalActive: false})
        }
    }
}
</script>
<style lang="scss">
    .modal{
        background-color: rgba(0, 0, 0, 0.5);
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        display: none;
        justify-content: center;
        align-items: center;
        &.show{
            display: flex;
        }
        &__content{
            background-color: #fff;
            max-width: 600px;
            width: 100%;
            padding: 10px;
            border-radius: 5px;
        }
        &__header{
            position: relative;
            margin-bottom: 20px;
        }
        &__title{
            font-weight: bold;
            font-size: 18px;
        }
        &__text{
            margin-bottom: 10px;
        }
        &__btn{
            margin-right: 10px;
        }
        &__close{
            position: absolute;
            top: 0px;
            right: 0px;
            cursor: pointer;
        }
    }

</style>