<template>
    <div class="blog__sort sort">
        Сортировка: 
        <select name="sort" class="sort__select" @change="changeSort($event)">
            <option :value="item.value" v-for="item in sort" :key="item.id">{{ item.label}}</option>
        </select>
    </div>
</template>
<script>
export default {
    name: 'BlogSort',
    data(){
        return {
            sort: [
                {id: 0, label: 'По умолчанию', value: ''},
                {id: 1, label: 'По названию: А - Я', value: 'name_asc'},
                {id: 2, label: 'По названию: Я - А', value: 'name_desc'}
            ]
        }
    },
    methods: {
        changeSort(e){
            this.$emit("update:modelValue", e.target.value)
        }
    }
}
</script>
<style lang="scss">
.sort{
  margin-bottom: 20px;
  text-align: right;
}
</style>