import { createStore } from 'vuex'

export default createStore({
  state: {
    posts: []
  },
  getters: {
    getPosts: state => state.posts
  },
  mutations: {
    LOAD_DATA(state,data){
      state.posts = data
      state.posts.map(item => {
        item.excerpt = getExcerpt(item.body)
      })
    },
    EDIT_TITLE_POST(state, {newTitle, post}){
      let curPost = state.posts.find(item => item.id === post);
      curPost.title = newTitle;
    },
    EDIT_DESCR_POST(state, {newDescr, post}){
      let curPost = state.posts.find(item => item.id === post);
      curPost.body = newDescr
      curPost.excerpt = getExcerpt(newDescr)
    },
    DELETE_POST(state, post){
      state.posts = state.posts.filter((item)=> item.id !== post)
    },
    ADD_POST(state){
      state.posts.push(
        {
          id: Date.now(),
          userId: 1,
          title: '',
          body: '',
          excerpt: ''
        }
      )
      state.posts.sort(sortIdDesc)
    },
    SET_POSTS(state,data){
      state.posts = data
    },
    SORT_POSTS(state, key){
      switch (key){
				case 'name_asc':
					state.posts.sort(sortByTitleAsc);
					break;
				case 'name_desc': 
					state.posts.sort(sortByTitleDesc);
					break;
				default:
					state.posts.sort(sortDefault);
			}
    }
  },
  actions: {
    async loadData({commit}){
        fetch('https://jsonplaceholder.typicode.com/posts?_limit=5')
        .then((response) => response.json())
        .then((data) => {
          commit('LOAD_DATA', data)
          this.dispatch('setStorage');
        });
    },
    editTitlePost({commit}, {newTitle, post}){
      commit('EDIT_TITLE_POST', {newTitle, post})
      this.dispatch('setStorage');
    },
    editDescrPost({commit}, {newDescr, post}){
      commit('EDIT_DESCR_POST', {newDescr, post})
      this.dispatch('setStorage');
    },
    deletePost({commit}, post){
      commit('DELETE_POST', post)
      this.dispatch('setStorage');
    },
    addPost({commit}){
      commit('ADD_POST')
      this.dispatch('setStorage');
    },
    setStorage({state}){
      localStorage.posts = JSON.stringify(state.posts);
    },
    setPosts({commit}, data){
      commit('SET_POSTS', data)
    },
    sortPosts({commit}, key){
			commit('SORT_POSTS', key);
		}
  }
})

function getExcerpt(text){
  return text.length>50 ? text.split(0,50)[0] + '...' : text
}
let sortByTitleAsc = function(d1,d2) { return (d1.title.toLowerCase() > d2.title.toLowerCase()) ? 1 : -1;}
let sortByTitleDesc = function(d1,d2) { return (d1.title.toLowerCase() < d2.title.toLowerCase()) ? 1 : -1;}
let sortDefault = function(d1,d2) {return (d1.id > d2.id) ? 1 : -1;}
let sortIdDesc = function(d1,d2) {return (d1.id < d2.id) ? 1 : -1;}